@import '../../scss/variables';

.myModal {
  position: fixed;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  display: none;
  background-color: rgba(0, 0, 0, 0.4);
  transition: .5s linear;

}

.myModal.active {
  display: flex;
  justify-content: center;
  align-items: center;

}

.myModalContent {
  padding: 25px;
  background: white;
  border-radius: 25px;
  min-width: 250px;


}

